import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { NzMessageService } from 'ng-zorro-antd';
import { UserAnswer, UserQuestion } from '@shared/service-proxies/entity';
import { UserAnswerService, UserQuestionService } from '@shared/service-proxies/marketing-service';

import { AppComponentBase } from '@shared/app-component-base';

@Component({
    selector: 'app-page-user-answer',
    templateUrl: './user-answer.component.html'
})
export class UserAnswerComponent extends AppComponentBase implements OnInit {
    data: UserAnswer[] = [];
    question: UserQuestion;
    questionId: string;
    questions = [
        {
            no: 1,
            text: '?',
            answers: [
                { label: '', checked: false },
                { label: '', checked: false },
                { label: '', checked: false },
                { label: '', checked: false }
            ]
        },
        {
            no: 2,
            text: '?',
            answers: [
                { label: '11', checked: false },
                { label: '15', checked: false },
                { label: '5', checked: false }
            ]
        },
        {
            no: 3,
            text: '?',
            answers: [
                { label: '20', checked: false },
                { label: '2070', checked: false },
                { label: '70150', checked: false },
                { label: '150', checked: false }
            ]
        },
        {
            no: 4,
            text: '?',
            answers: [
                { label: '30', checked: false },
                { label: '3080', checked: false },
                { label: '80', checked: false }
            ]
        },
        {
            no: 5,
            text: '?',
            answers: [
                { label: '30', checked: false },
                { label: '3080', checked: false },
                { label: '80', checked: false }
            ]
        },
        {
            no: 6,
            text: '?',
            answers: [
                { label: '', checked: false },
                { label: '', checked: false },
                { label: '', checked: false },
                { label: '', checked: false }
            ]
        },
        {
            no: 7,
            text: '?',
            answers: [
                { label: '', checked: false },
                { label: '', checked: false },
                { label: '', checked: false },
                { label: '', checked: false }
            ]
        },
        {
            no: 8,
            text: '?',
            answers: [
                { label: '', checked: false },
                { label: '', checked: false },
                { label: '', checked: false }
            ]
        },
        {
            no: 9,
            text: '?',
            answers: [
                { label: '', checked: false },
                { label: '', checked: false },
                { label: '', checked: false }
            ]
        },
        {
            no: 10,
            text: '?',
            answers: [
                { label: '', checked: false },
                { label: '', checked: false },
                { label: '', checked: false }
            ]
        }
    ];

    constructor(injector: Injector, public msg: NzMessageService
        , private _userAnswerService: UserAnswerService
        , private _userQuestionService: UserQuestionService
        , public route: ActivatedRoute) {
        super(injector);
        this.questionId = this.route.snapshot.params['id'];
    }

    ngOnInit() {
        this.getQuestion();
        this.getAanswerData();
    }

    getQuestion() {
        this._userQuestionService.get(this.questionId).subscribe(result => {
            this.question = result;
        });
    }

    getAanswerData() {
        this._userAnswerService.getByQuestionId(this.questionId).subscribe((result: UserAnswer[]) => {
            this.data = result;
            if (this.data != undefined) {
                for (const item of this.data) {
                    this.setQuestion(item);
                }
            }
        })
    }

    setQuestion(answer: UserAnswer) {
        for (const question of this.questions) {
            if (answer.answerSqe == question.no) {
                for (const aitem of question.answers) {
                    if (aitem.label == answer.content) {
                        aitem.checked = true;
                        return;
                    }
                }
                return;
            }
        }
    }
}



